/*===== GOOGLE FONTS =====*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
/*===== VARIABLES CSS =====*/
:root{
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root{
  --first-color: #4070F4;
  --second-color: #0E2431;
}

/*===== Fuente y tipografia =====*/
:root{
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}
@media screen and (min-width: 768px){
  :root{
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root{
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root{
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*,::before,::after{
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body{
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  color: var(--second-color);
}
h1,h2,p{
  margin: 0;
}
ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
a{
  text-decoration: none;
}
img{
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section-title{
  position: relative;
  font-size: var(--h2-font-size);
  color: var(--first-color);
  margin-top: var(--mb-2);
  margin-bottom: var(--mb-4);
  text-align: center;
}
.section-title::after{
  position: absolute;
  content: "";
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: var(--first-color);
}
.section{
  padding-top: 3rem;
  padding-bottom: 2rem;
}

/*===== LAYOUT =====*/
.bd-grid{
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: var(--mb-2);
  margin-right: var(--mb-2);
}
.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(146,161,176,.15);
}

/*===== NAV =====*/
.nav{
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: var(--font-semi);
}
@media screen and (max-width: 768px){
  .nav__menu{
    position: fixed;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: var(--second-color);
    transition: .5s;
  }
}
.nav__item{
  display: block;
  margin-bottom: var(--mb-4);
}
.nav__link{
  position: relative;
  color: #fff;
}
.nav__link:hover{
  position: relative;
  cursor: pointer;
}
.nav__link:hover::after{
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}
.nav__logo{
  color: var(--first-color);
}
.nav__logo:hover{
  cursor: pointer;
}
.nav__toggle{
  color: var(--second-color);
  font-size: 1.5rem;
  cursor: pointer;
}

/*Active menu*/
.active::after{
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

/*=== Show menu ===*/
.show{
  right: 0;
}

/*===== HOME =====*/
.home{
  height: calc(100vh - 3rem);
  row-gap: 1rem;
}
.home__data{
  align-self: center;
}
.home__title{
  font-size: var(--big-font-size);
  margin-bottom: var(--mb-5);
}
.home__title-color{
  color: var(--first-color);
}
.home__social{
  display: flex;
  flex-direction: column;
}
.home__social-icon{
  width: max-content;
  margin-bottom: var(--mb-2);
  font-size: 1.5rem;
  color: var(--second-color);
}
.home__social-icon:hover{
  color: var(--first-color);
}
.home__img{
  position: absolute;
  right: 0;
  bottom: 0;
  width: 295px;
}

.animate_headline {
  text-transform: uppercase;
}

/*BUTTONS*/
.button{
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: .75rem 2.5rem;
  font-weight: var(--font-semi);
  border-radius: .5rem;
}
.rotate_180{
  -webkit-transition: -webkit-transform .8s ease-in-out;
  transition: transform .8s ease-in-out;
}
.button:hover{
  cursor: pointer;
  box-shadow: 0 10px 36px rgba(0,0,0,.15);
}
.button:hover .rotate_180{
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

/* ===== ABOUT =====*/
.about__container{
  row-gap: 2rem;
  padding-top: 1%;
  text-align: center;
}
.about__subtitle{
  margin-bottom: var(--mb-2);
}
.about__img{
  justify-self: center;
}
.about__img img{
  width: 800px;
  border-radius: .9rem;
}
.about__text {
  font-size: 1.2rem;
  margin-bottom: var(--mb-2);
}

/* ===== WORK =====*/
.work__container{
  row-gap: 2rem;
  text-align: center;
}
.work__subtitle{
  font-size: 1.5rem;
  margin-bottom: .05rem;
  text-align: center;
}
.work__subtitle_position{
  font-size: 1rem;
  color: var(--first-color);
  text-align: center;
}
.work__subtitle_date{
  font-size: .8rem;
  margin-bottom: .8rem;
  text-align: center;
  text-decoration: underline;
}
.work__text {
  font-size: 1rem;
  margin-bottom: var(--mb-2);
}

/* ===== PROJECT =====*/
.project__container{
  row-gap: 2rem;
  text-align: center;
}
.project__subtitle{
  font-size: 1.2rem;
  margin-bottom: var(--mb-2);
  text-align: center;
}
.project__text {
  font-size: 0.8rem;
  color: var(--first-color);
  margin-bottom: var(--mb-2);
  text-align: center;
  font-style: italic;
}
.project__img{
  transition: 1s;
  cursor: pointer;
  box-shadow: 0 4px 25px rgba(69, 69, 245, 0.1);
  padding: 0.5rem;
  border-radius: 1.6rem;
}
.project__img:hover{
  transform: scale(2);
  box-shadow: 0 4px 25px rgba(69, 69, 245, 0.5);
}

/* ===== SKILLS =====*/
.skills__container{
  row-gap: 2rem;
  text-align: center;
}
.skills__subtitle{
  color: black;
  padding: .75rem .5rem;
  font-weight: var(--font-semi);
  
}
.skills__text{
  margin-bottom: var(--mb-4);
}
.skills__data{
  display: flex;
  width: 33%;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: var(--font-semi);
  padding: .5rem 1rem;
  margin-bottom: var(--mb-4);
  border-radius: .5rem;
  box-shadow: 0 4px 25px rgba(14,36,49,.15);
}

.skills__names{
  display: flex;
  align-items: center;
}

/* .button-group{
  box-shadow: 0 4px 25px rgba(14,36,49,.15);
} */
.skills__img{
  /* box-shadow: 0 4px 25px rgba(14,36,49,.15); */
  border-radius: .5rem;
  overflow: hidden;
  margin: 18px 0;
}

.skill-image{
  margin-left: auto;
  margin-right: auto;
  width: 5rem;
}

.gallery-menu{
  text-align: center;
  margin-bottom: var(--mb-4);
}

/* ===== FOOTER =====*/
.footer{
  background-color: black;
  color: #fff;
  text-align: center;
  font-weight: var(--font-semi);
  padding: 2rem 0;
}
.footer__title{
  font-size: 2rem;
  margin-bottom: var(--mb-4);
}
.footer__social{
  margin-bottom: var(--mb-4);
}
.footer__icon{
  font-size: 1.5rem;
  color: #fff;
  margin: 0 var(--mb-2)
}

.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  
}

/* Modal Content */
.modal-content {
  background-color: #fff;
  margin: auto;
  padding: 10px 16px;
  border: 1px solid var(--first-color);
  border-radius: 0.6rem;
  width: 40%;
}

.modal__title{
  color: black;
  font-weight: var(--font-semi);
  font-size: 1.6rem;
  margin-bottom: 2px;
  
}
.modal__close{
  float: right;
  font-size: 1.6rem;
  color: red;
  -webkit-transition: -webkit-transform .8s ease-in-out;
  transition: transform .8s ease-in-out;
}
.modal__close:hover{
  cursor: pointer;
  -webkit-transform: rotate(360deg) scale(1.4, 1.4);
  transform: rotate(360deg) scale(1.4, 1.4);
}
.modal__subtitle{
  color: black;
  font-weight: var(--normal-font-size);
  font-size: 1.2rem;
  margin-bottom: var(--mb-6);
  
  font-style: italic;
}

.modal__text{
  font-size: 1rem;
  margin-bottom: var(--mb-2);
}

.modal-btn{
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: .50rem 1rem;
  font-weight: var(--font-semi);
  border-radius: .5rem;
}

.blue__it{
  color: var(--first-color);
  font-style: italic;
  font-weight: bold;
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px){
  body{
    margin: 0;
  }
  .section{
    padding-top: 4rem;
    padding-bottom: 3rem;
  }
  .section-title{
    margin-bottom: var(--mb-6);
  }
  .section-title::after{
    width: 80px;
    top: 3rem;
  }

  .nav{
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list{
    display: flex;
    padding-top: 0;
  }
  .nav__item{
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle{
    display: none;
  }
  .nav__link{
    color: var(--second-color);
  }

  .home{
    height: 100vh;
  }
  .home__data{
    align-self: flex-end;
  }
  .home__social{
    padding-top: 0;
    padding-bottom: 2.5rem;
    flex-direction: row;
    align-self: flex-end;
  }
  .home__social-icon{
    margin-bottom: 0;
    margin-right: var(--mb-4);
  }
  .home__img{
    width: 457px;
    bottom: 15%;
  }

  .about__container, .work__container{
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    text-align: initial;
  }
  .project__container{
    grid-template-columns: repeat(3,1fr);
    align-items: center;
    text-align: initial;
  }
  .about__img img{
    width: 300px;
  }
  /* .work__container{
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2,1fr);
    column-gap: 2rem;
  } */
  .skills__container{
    grid-template-columns: repeat(6, 1fr);
    /* grid-template-rows: repeat(2,1fr); */
    column-gap: 2rem;
  }
}

@media screen and (min-width: 1024px){
  .bd-grid{
    margin-left: auto;
    margin-right: auto;
  }
  .home__img{
    right: 10%;
  }
}

